import React from 'react';
import { Link } from 'react-router-dom';
import { Helmet } from "react-helmet";

export default class Add3dProductViewerToShopifyBlog extends React.Component {
    render() {
        return (
            <div className='blog-page'>
                <Helmet>
                    <meta charSet="utf-8" />
                    <title>Display products in 3D and augmented reality on your Shopify store</title>
                    <link rel="canonical" href="https://www.ezyvue.com/blog/add3DViewerToShopify" />
                </Helmet>
                <main>
                    <h1>Display products in 3D and augmented reality on your Shopify store</h1>
                    <img alt='3D model of a product displayed on Shopify' src={require('assets/images/blog/shopify/finalimage.jpg')} loading='lazy' />
                    <p style={{ lineHeight: '1.0' }}>David Long<br />
                        <span style={{ fontSize: 'small' }}>22nd June 2023</span>
                    </p>
                    <hr />
                    <p>
                        With the Ezyvue platform your customers can view and interact with a realistic 3D model of a product and see it in their home with augmented reality.
                        <a href='https://www.shopify.com/'> Shopify </a> is an all-in-one commerce platform that powers millions of businesses worldwide.
                        Shopify allows you to upload and display 3D models of products in the same way as images but it lacks the more advanced features of the Ezyvue viewer.
                        In this guide, I&apos;ll show you how easy it is to embed the Ezyvue 3D viewer and show a product in 3D and AR on your Shopify store.    
                    </p> 
                    <h3>Upload a 3D model to the Ezyvue platform</h3>
                    <p>
                        Firstly you&apos;ll need to create a free/trial Ezyvue account and upload your 3D model.
                        Go to the <Link to='/pricing'>pricing page</Link> and sign up.
                        Click on the link in the email sent to you to activate your account and you&apos;re ready to log in.
                    </p>
                    <p>
                        Next, create a new project and upload a 3D model file from your computer. From the Project menu select New Project.
                        Fill in the name and description and select a single file or a directory if the 3D model has additional asset files such as textures.
                        Hit the Submit button and a new project will be created which can be viewed on the Manage Projects page.
                    </p>
                    <p>
                        You can now preview the uploaded model, on the Manage Projects page, click the eye icon next to the project to preview the model.
                    </p>
                    <p>
                        As the model loads, a placeholder image is displayed. To change this to an image of the model, open Ezyvue Studio by clicking the edit button (pencil icon) next to the project on the Manage Products page.
                        Before taking a snapshot of the model, click the viewport tab (square icon) and untick the grid checkbox.
                        Next, click the General Settings tab (the gear icon) and adjust the camera in the main viewport then click the Take Snapshot button.
                    </p>
                    <h3>Embed the viewer on your Shopify store</h3>
                    <p>
                        For this guide, I&apos;ve created a simple camera store using one of the free templates. I deleted the featured product section and changed some text and images.
                        <img alt='' src={require('assets/images/blog/shopify/newsite.jpg')} loading='lazy' />
                    </p>
                    <p>
                        The 3D model will be displayed as a featured product in a section below the main page image section
                        To do this move the mouse over the section, click the add section button and select Custom Liquid.
                        <img alt='' src={require('assets/images/blog/shopify/addliquid.jpg')} loading='lazy' />
                    </p>
                    <p>
                        We will use the liquid section to create a two-column display with the product description on the left and the 3D viewer on the right.
                        Copy the code below and paste in to the Custom Liquid textbox. 
                        <pre>{`
<style>
.row {
  display: flex;
}

.column {
  flex: 50%;
padding: 2%
}
</style>
<div class="row">
  <div class="column">
    <h1>Featured Product </h1>
    <h2>Canon Rebel t7i </h2>
    <p>Unlock Your Potential. Take your photos to the next level with the image quality and near-instant autofocus of the EOS Rebel T7i camera. A 45-point all cross-type AF system* and optical viewfinder</p>
<b style="font-size:large">Today only $533<br/>
Save $300!</b>
  </div>
  <div class="column">
    Paste embed code here...
  </div>
</div>
`}</pre>
                    The end result should look something like the image below.
                    <img alt='' src={require('assets/images/blog/shopify/productsection.jpg')} loading='lazy' />
                    </p>
                    <p>
                        The next step is to display your uploaded model in the righthand column.
                        Go to the Integrate Viewer page on Ezyvue and in the HTML Code section and select a project.
                        <img alt='' src={require('assets/images/blog/shared/code.jpg')} loading='lazy' />
                        Click the copy button below the Html Code window, delete the &#39;Paste embed code here...&#39; text, and paste the copied code.
                        <img alt='' src={require('assets/images/blog/shopify/finaledit.jpg')} loading='lazy' />
                        The viewer should load and display the project snapshot image.
                    </p>
                    <p>
                        Save the edits by clicking the Save button on the main menu bar and preview your site to admire your work.
                        <img alt='3D model of a product displayed on Shopify' src={require('assets/images/blog/shopify/finalimage.jpg')} loading='lazy' />
                    </p>
                    <h3>Ezyvue Studio</h3>
                    Using Ezyvue Studio you can customize the scene, change the background, add a reflection and shadows, change the lighting,
                    add 3D text annotations, create product variants and much more. See the Ezyvue Studio documentation in the help center. 
                    <h3>Adding a 3D model to your product page</h3>
                    This guide demonstrated how to add a specific 3D model using Ezyvue to any page on a Shopify store. 
                    To show a 3D model on the product page when a product is selected see the &#39;Integrate with Shopify&#39; guide in the help center.  
                    <br />
                    <br />
                    <hr />
                    <h3>Related blogs</h3>
                    <Link to='/blog/enhanceWebsiteWithA3DViewer'>Enhancing your online store with a 3D viewer</Link>
                    <br />
                    <Link to='/blog/addingA3dViewerToAWebsite'>Display products in 3D and AR on your website</Link>
                    <br />
                    <br />
                </main>
            </div>
        );
    }
}